// login/index.tsx
import * as React from 'react';
// import { inject, observer } from 'mobx-react';
import LoginTemplate from '../../src/components/LoginTemplate'
import nextCookie from 'next-cookies'
import { inject, observer } from 'mobx-react';
// import { AuthStore } from '../../src/stores/AuthStore';
// interface Props {
//   authStore?: any
// }

const LoginPage = ({authStore, jwt}:any) => {
  authStore.fetch(jwt)
  return (
    <div>
      <h1>login index page: {authStore.userData.email}</h1>
      <LoginTemplate />
    </div>
  )
}

LoginPage.getInitialProps = async (ctx:any) => {
  const { jwt } = nextCookie(ctx)
  return { jwt: jwt }
}

export default inject('authStore')(observer(LoginPage)) 